"use client";

import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const MotionDiv = motion.div;
const MotionButton = motion.button;

export default function ExtraButton() {
  const [isOpen, setIsOpen] = useState(false);
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  if (!isMounted) return null;

  return (
    <div className="relative">
      <MotionButton
        onClick={() => setIsOpen(!isOpen)}
        className={`text-tiny text-[var(--muted)] hover:text-blue-500 transition-colors duration-200 ${isOpen ? 'text-blue-500' : ''}`}
      >
        Extra {isOpen ? '‚àí' : '+'}
      </MotionButton>
      
      <AnimatePresence>
        {isOpen && (
          <MotionDiv
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.2 }}
            className="absolute left-0 top-6 min-w-[120px]"
          >
            <div className="space-y-1">
              <a 
                href="/uses" 
                className="block px-2 py-1 text-tiny text-[var(--muted)] hover:text-blue-500 transition-colors duration-200"
              >
                Uses
              </a>
              <a 
                href="/books" 
                className="block px-2 py-1 text-tiny text-[var(--muted)] hover:text-blue-500 transition-colors duration-200"
              >
                Books
              </a>
              <a 
                href="/setup" 
                className="block px-2 py-1 text-tiny text-[var(--muted)] hover:text-blue-500 transition-colors duration-200"
              >
                Setup
              </a>
            </div>
          </MotionDiv>
        )}
      </AnimatePresence>
    </div>
  );
}
